import mongoose from 'mongoose';

const trainSchema = new mongoose.Schema(
  {
    TrainNumber: String,
    TrainNameEnglish: String,
    TrainNameHindi: String,
    Ref: String,
    SrcCode: String,
    SrcNameEnglish: String,
    SrcNameHindi: String,
    DestCode: String,
    DestNameEnglish: String,
    DestNameHindi: String,
    STA: String,
    STD: String,
    LateBy: String,
    ETA: String,
    ETD: String,
    PFNo: String,
    Status: String,
    TypeAorD: { type: String, enum: ['A', 'D'] },
    CoachList: [String]
  },
  { timestamps: true }
);

export default mongoose.models.Train || mongoose.model('Train', trainSchema);
