import { NextRequest, NextResponse } from 'next/server';
import dbConnect from '@/lib/dbConnect';
import Train from '@/models/trainModel';

// GET all trains
export async function GET(req: NextRequest) {
  try {
    await dbConnect();
    const trains = await Train.find().sort({ createdAt: -1 });
    return NextResponse.json({ success: true, trains });
  } catch (error) {
    return NextResponse.json({ success: false, message: 'Error fetching trains' }, { status: 500 });
  }
}

// POST new trains (bulk)
export async function POST(req: NextRequest) {
  try {
    await dbConnect();
    const data = await req.json();

    if (!Array.isArray(data)) {
      return NextResponse.json({ success: false, message: 'Payload should be an array' }, { status: 400 });
    }

    await Train.insertMany(data);
    return NextResponse.json({ success: true, message: 'Trains saved successfully' }, { status: 201 });
  } catch (error) {
    return NextResponse.json({ success: false, message: 'Failed to save trains' }, { status: 500 });
  }
}
